:root {
  --color-background: #0b1220;
  --color-surface: #111c2f;
  --color-surface-alt: #152036;
  --color-elevated: #1f2b44;
  --color-border: rgba(148, 163, 184, 0.18);
  --color-border-strong: rgba(148, 163, 184, 0.35);
  --color-text-primary: #f8fafc;
  --color-text-secondary: #cbd5f5;
  --color-text-muted: #94a3b8;
  --color-accent: #3b82f6;
  --color-accent-strong: #1d4ed8;
  --color-danger: #ef4444;
  --color-danger-soft: rgba(239, 68, 68, 0.15);
  --shadow-soft: 0 10px 30px rgba(15, 23, 42, 0.35);
  --shadow-accent: 0 12px 32px rgba(59, 130, 246, 0.35);
  --shadow-border: inset 0 0 0 1px rgba(148, 163, 184, 0.12);
  --max-width: 1120px;
  --font-primary: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  --transition: 0.18s cubic-bezier(0.4, 0, 0.2, 1);
}

*, *::before, *::after {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background: var(--color-background);
  color: var(--color-text-primary);
  font-family: var(--font-primary);
}

body {
  display: flex;
  flex-direction: column;
}

#__next {
  flex: 1;
  display: flex;
  flex-direction: column;
}

a {
  color: inherit;
  text-decoration: none;
  transition: color var(--transition), opacity var(--transition);
}

a:hover {
  opacity: 0.85;
}

button {
  font-family: var(--font-primary);
}

input, textarea, select {
  font-family: var(--font-primary);
}

.app-shell {
  min-height: 100vh;
  background: var(--color-background);
  display: flex;
  flex-direction: column;
}

.section-shell {
  width: 100%;
  padding: 64px 24px;
  display: flex;
  justify-content: center;
}

.section-container {
  width: 100%;
  max-width: var(--max-width);
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border-radius: 12px;
  border: 1px solid transparent;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.2;
  padding: 12px 20px;
  cursor: pointer;
  transition: transform var(--transition), box-shadow var(--transition), border-color var(--transition), background var(--transition);
}

.btn-primary {
  background: linear-gradient(135deg, var(--color-accent), var(--color-accent-strong));
  color: #fff;
  box-shadow: var(--shadow-accent);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 14px 36px rgba(59, 130, 246, 0.45);
}

.btn-secondary {
  background: rgba(59, 130, 246, 0.12);
  color: var(--color-text-primary);
  border-color: rgba(59, 130, 246, 0.28);
}

.btn-ghost {
  background: transparent;
  border-color: var(--color-border);
  color: var(--color-text-secondary);
}

.site-header-wrapper {
  position: sticky;
  top: 0;
  z-index: 50;
  transition: transform var(--transition);
}

.site-header-wrapper--hidden {
  transform: translateY(-100%);
}

.site-header-wrapper--visible {
  transform: translateY(0);
}

.site-header {
  height: 80px;
  display: flex;
  align-items: center;
  background: rgba(11, 18, 32, 0.85);
  backdrop-filter: blur(16px) saturate(140%);
  border-bottom: 1px solid rgba(148, 163, 184, 0.18);
  box-shadow: 0 1px 0 rgba(15, 23, 42, 0.45);
}

.site-header__inner {
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 28px;
  display: flex;
  align-items: center;
  gap: 32px;
}

.site-header__logo {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 700;
  font-size: 18px;
  color: var(--color-text-primary);
  letter-spacing: -0.01em;
}

.site-header__logo img {
  height: 54px;
  width: auto;
}

.site-header__nav {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-left: auto;
}

.site-header__link {
  font-size: 15px;
  font-weight: 500;
  color: var(--color-text-secondary);
  padding: 10px 14px;
  border-radius: 999px;
  transition: background var(--transition), color var(--transition);
}

.site-header__link:hover,
.site-header__link--active {
  color: var(--color-text-primary);
  background: rgba(59, 130, 246, 0.16);
}

.site-header__cta {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-left: 12px;
}

.card {
  background: var(--color-surface);
  border-radius: 16px;
  border: 1px solid var(--color-border);
  box-shadow: var(--shadow-border);
  padding: 24px;
}

.badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  border-radius: 999px;
  padding: 6px 12px;
  font-size: 13px;
  font-weight: 500;
  background: rgba(59, 130, 246, 0.15);
  color: #bfdbfe;
}

.section-heading {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 40px;
  text-align: center;
}

.section-heading--left {
  text-align: left;
  align-items: flex-start;
}

.section-heading__title {
  font-size: clamp(2rem, 3vw, 2.8rem);
  font-weight: 700;
  margin: 0;
}

.section-heading__subtitle {
  margin: 0;
  color: var(--color-text-secondary);
  font-size: 17px;
  line-height: 1.6;
}

.brand-strip {
  background: var(--color-surface-alt);
  border-radius: 16px;
  border: 1px solid rgba(148, 163, 184, 0.2);
  padding: 28px 24px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.brand-strip__list {
  display: flex;
  flex-wrap: wrap;
  gap: 28px;
  justify-content: center;
  align-items: center;
}

.brand-strip__item {
  opacity: 0.75;
  transition: opacity var(--transition);
}

.brand-strip__item:hover {
  opacity: 1;
}

.feature-grid {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.feature-card {
  background: var(--color-surface-alt);
  border-radius: 18px;
  padding: 24px;
  border: 1px solid rgba(148, 163, 184, 0.2);
  display: flex;
  flex-direction: column;
  gap: 14px;
  transition: transform var(--transition), box-shadow var(--transition), border-color var(--transition);
}

.feature-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-soft);
  border-color: rgba(148, 163, 184, 0.32);
}

.feature-card__icon {
  width: 46px;
  height: 46px;
  border-radius: 12px;
  display: grid;
  place-items: center;
  background: rgba(59, 130, 246, 0.12);
  color: #93c5fd;
  font-size: 20px;
  font-weight: 600;
}

.feature-card__title {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
}

.feature-card__text {
  margin: 0;
  color: var(--color-text-secondary);
  line-height: 1.6;
}

.steps-grid {
  display: grid;
  gap: 24px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.step-card {
  background: var(--color-surface);
  border-radius: 18px;
  border: 1px solid rgba(148, 163, 184, 0.18);
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.step-card__number {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  background: rgba(59, 130, 246, 0.18);
  color: #bfdbfe;
  display: grid;
  place-items: center;
  font-weight: 700;
}

.step-card__title {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
}

.step-card__text {
  margin: 0;
  color: var(--color-text-secondary);
  line-height: 1.6;
}

.cta-banner {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.35), rgba(59, 87, 231, 0.45));
  border-radius: 20px;
  border: 1px solid rgba(148, 163, 184, 0.24);
  padding: 36px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: flex-start;
}

.cta-banner__title {
  font-size: 26px;
  font-weight: 700;
  margin: 0;
}

.cta-banner__text {
  margin: 0;
  color: rgba(248, 250, 252, 0.9);
  line-height: 1.6;
}

.cv-board {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.cv-card {
  background: var(--color-surface);
  border-radius: 18px;
  border: 1px solid rgba(148, 163, 184, 0.2);
  padding: 22px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  text-align: left;
  transition: transform var(--transition), box-shadow var(--transition), border-color var(--transition);
}

.cv-card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-soft);
  border-color: rgba(148, 163, 184, 0.32);
}

.cv-card__title {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  font-weight: 600;
  font-size: 17px;
}

.cv-card__meta {
  color: var(--color-text-secondary);
  font-size: 14px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.cv-card__delete {
  background: var(--color-danger-soft);
  border: 1px solid rgba(239, 68, 68, 0.35);
  color: #fca5a5;
  border-radius: 10px;
  padding: 6px 10px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: background var(--transition);
}

.cv-card__delete:hover:not(:disabled) {
  background: rgba(239, 68, 68, 0.3);
}

.cv-card__delete:disabled {
  opacity: 0.65;
  cursor: not-allowed;
}

.create-card {
  background: rgba(59, 130, 246, 0.12);
  border: 1px dashed rgba(59, 130, 246, 0.45);
  color: #bfdbfe;
  display: flex;
  flex-direction: column;
  gap: 12px;
  justify-content: center;
  transition: transform var(--transition), box-shadow var(--transition);
}

.create-card span {
  color: inherit;
}

.create-card:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: var(--shadow-accent);
}

.auth-shell {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 48px 16px;
  background: radial-gradient(circle at 20% 20%, rgba(59, 130, 246, 0.22), transparent 55%),
    radial-gradient(circle at 80% 10%, rgba(99, 102, 241, 0.18), transparent 60%), var(--color-background);
}

.auth-card {
  width: 100%;
  max-width: 420px;
  background: rgba(17, 28, 47, 0.78);
  border-radius: 20px;
  border: 1px solid rgba(148, 163, 184, 0.25);
  box-shadow: 0 32px 70px rgba(15, 23, 42, 0.45);
  padding: 36px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  color: var(--color-text-primary);
}

.auth-card__header {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.auth-card__header img {
  height: 72px;
  margin: 0 auto;
}

.auth-card__title {
  font-size: 26px;
  font-weight: 700;
  margin: 0;
}

.auth-card__subtitle {
  color: var(--color-text-secondary);
  margin: 0;
  font-size: 15px;
}

.form-control {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-control label {
  font-size: 14px;
  font-weight: 600;
  color: var(--color-text-secondary);
}

.form-control input {
  width: 100%;
  padding: 14px 16px;
  border-radius: 12px;
  border: 1px solid rgba(148, 163, 184, 0.35);
  background: rgba(11, 18, 32, 0.65);
  color: var(--color-text-primary);
  font-size: 15px;
  transition: border-color var(--transition), box-shadow var(--transition);
}

.form-control input:focus {
  outline: none;
  border-color: rgba(59, 130, 246, 0.55);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.18);
}

.form-feedback {
  font-size: 13px;
  color: #fca5a5;
}

.form-success {
  font-size: 13px;
  color: #34d399;
}

.auth-actions {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.auth-switch {
  display: flex;
  justify-content: center;
  gap: 8px;
  font-size: 14px;
  color: var(--color-text-secondary);
}

.auth-switch button {
  background: none;
  border: none;
  color: var(--color-accent);
  font-weight: 600;
  cursor: pointer;
}

.cv-shell {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: var(--color-background);
}

.cv-panels {
  flex: 1;
  display: flex;
  background: linear-gradient(120deg, rgba(11, 18, 32, 0.96), rgba(15, 27, 46, 0.88));
}

.cv-editor-panel {
  height: 100vh;
  overflow: auto;
  padding: 24px;
  background: rgba(11, 18, 32, 0.92);
}

.cv-preview-panel {
  height: 100vh;
  background: var(--color-surface);
  border-left: 1px solid rgba(148, 163, 184, 0.18);
  display: flex;
  flex-direction: column;
}

.cv-toolbar {
  background: rgba(15, 23, 42, 0.9);
  border-bottom: 1px solid rgba(148, 163, 184, 0.18);
  padding: 18px 24px;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.cv-toolbar__top {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  align-items: center;
}

.cv-warning {
  margin: 0 24px;
  border-radius: 12px;
  border: 1px solid rgba(250, 204, 21, 0.45);
  background: rgba(251, 191, 36, 0.12);
  padding: 12px 16px;
  color: #facc15;
  font-size: 14px;
}

.cv-toolbar input {
  flex: 1 1 220px;
  min-width: 180px;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid rgba(148, 163, 184, 0.28);
  background: rgba(11, 18, 32, 0.6);
  color: var(--color-text-primary);
  font-size: 14px;
}

.cv-preview-scroll {
  flex: 1;
  overflow: auto;
  padding: 16px 24px 32px;
  background: linear-gradient(180deg, rgba(248, 250, 252, 0.98), rgba(226, 232, 240, 0.96));
}

.preview-cv {
  transform: scale(0.82);
  transform-origin: top center;
  transition: transform var(--transition);
}

@media (max-width: 1200px) {
  .preview-cv {
    transform: scale(0.65);
  }
}

@media (max-width: 768px) {
  .preview-cv {
    transform: scale(0.55);
  }
}

.footer-shell {
  background: radial-gradient(circle at top, rgba(59, 130, 246, 0.08), transparent 55%), var(--color-background);
  border-top: 1px solid rgba(148, 163, 184, 0.14);
  padding: 72px 0 40px;
}

.footer-inner {
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 24px;
}

.footer-grid {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  margin-bottom: 48px;
}

.footer-brand h3 {
  font-size: 26px;
  margin-bottom: 16px;
  font-weight: 700;
}

.footer-brand p {
  color: var(--color-text-secondary);
  line-height: 1.6;
  margin: 0 0 24px;
}

.footer-column h4 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 18px;
}

.footer-link,
.footer-list li {
  color: var(--color-text-secondary);
  font-size: 15px;
  line-height: 1.6;
  display: block;
  margin-bottom: 10px;
  transition: color var(--transition);
}

.footer-link:hover,
.footer-list li:hover {
  color: var(--color-text-primary);
}

.footer-bottom {
  border-top: 1px solid rgba(148, 163, 184, 0.18);
  padding-top: 28px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: space-between;
  color: var(--color-text-muted);
  font-size: 14px;
}

.footer-bottom__tags {
  display: flex;
  gap: 18px;
}

@media (max-width: 640px) {
  .footer-bottom {
    flex-direction: column;
    align-items: flex-start;
  }
}

.text-muted {
  color: var(--color-text-muted);
}

.text-secondary {
  color: var(--color-text-secondary);
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.grid-two-columns {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
}

.hero-shell {
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid rgba(148, 163, 184, 0.12);
}

.hero-background {
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 20% 20%, rgba(59, 130, 246, 0.18), transparent 55%),
              radial-gradient(circle at 80% 10%, rgba(99, 102, 241, 0.16), transparent 50%),
              linear-gradient(180deg, rgba(11, 18, 32, 0.6), rgba(11, 18, 32, 0.95));
}

.hero-content {
  position: relative;
  z-index: 1;
  padding: 96px 24px 80px;
  display: flex;
  justify-content: center;
}

.hero-inner {
  width: 100%;
  max-width: var(--max-width);
  display: grid;
  gap: 48px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  align-items: center;
}

.hero-title {
  font-size: clamp(2.5rem, 5vw, 3.75rem);
  font-weight: 800;
  line-height: 1.05;
  letter-spacing: -0.02em;
  margin: 0;
}

.hero-subtitle {
  display: flex;
  flex-direction: column;
  gap: 18px;
  margin-top: 24px;
  color: var(--color-text-secondary);
  font-size: 18px;
  line-height: 1.6;
}

.hero-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 32px;
}

.surface-panel {
  background: var(--color-surface-alt);
  border-radius: 20px;
  border: 1px solid rgba(148, 163, 184, 0.18);
  padding: 32px;
  box-shadow: 0 24px 45px rgba(15, 23, 42, 0.35);
}

.table-list {
  display: grid;
  gap: 16px;
}

.table-row {
  background: var(--color-surface-alt);
  border-radius: 14px;
  border: 1px solid rgba(148, 163, 184, 0.16);
  padding: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  transition: transform var(--transition), box-shadow var(--transition), border-color var(--transition);
}

.table-row:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-soft);
  border-color: rgba(148, 163, 184, 0.35);
}

@media (max-width: 900px) {
  .hero-inner {
    grid-template-columns: 1fr;
  }

  .hero-content {
    padding: 72px 24px 64px;
  }
}

@media (max-width: 640px) {
  .card {
    padding: 20px;
  }

  .hero-actions {
    flex-direction: column;
    align-items: stretch;
  }

  .btn {
    width: 100%;
    justify-content: center;
  }
}
